@media (max-width: 2560px) {

    header{
 
        font-size: 4rem;
    }


    nav button {
        padding: 10px 20px; 
        font-size: 2rem;
  
    
    }


    #category-header {

        font-size: 2.5em;
    }

    #articles-container {
        column-count: 5;
        column-gap: 20px;
        padding: 20px;
    }

    .article {

        padding: 20px;
    }



    .article h2 {
        font-size: 2em;
        margin: 15px;
  
    }
    
    .article p {

        font-size: 1.4em;
        color: black;

    }
 
    .article a {
        font-size: 1.3rem;
    }
    footer{

        padding: 30px 60px;
    }
    
    footer p {
        font-size: 150%; 
    }

    #back-to-top {

        height: 9%;
    
    }
    

    .loading-spinner {
        width: 30px;
        height: 30px;

    }
 

}

